.popupContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    .popup {
      background-color: #fff;
      border-radius: 10px;
      box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
      padding: 20px;
      max-width: 400px;
      width: 80%;
      h2 {
        margin-top: 0;
         margin-bottom: 10px;
         font-size: 24px;
         margin-bottom: 20px;
         color: #222;
       }
       label {
        margin-top: 20px;
       }
      .action {
        margin-top: 20px;
        button {
          background-color: #007bff;
          color: #fff;
          border: none;
          padding: 10px 20px;
          cursor: pointer;
          font-size: 16px;
          border-radius: 5px;
          &:hover {
            background-color: #0056b3;
          }
        }
      }
    }
  }
  
  